









import ut from '../../utils/index.js';
export default {
	data() {
		return {
			name:''
		}
	},
	onLoad() {

	},
	methods: {
		go_mine_adress(){
			wx.navigateTo({
				url: '../mine/adress'
			})
		},
		uploadimg(){
			ut.uploadimg({
				callback:res=>{
					wx.setStorageSync('headurl',res);
					this.headurl=res;
				}
			})
		},
		req_updateName(){
			var re = new RegExp("[\\u4e00-\\u9fa5]", "");
			var re1 = new RegExp("^[a-zA-Z]+$"); 
			if(!this.name){
				ut.totast('请输入昵称');
				return;
			}
			if((!re.test(this.name.split('')[0])&&!re1.test(this.name.split('')[0])) ||this.checkstr(this.name)<4 ||this.checkstr(this.name)>16){
				ut.totast('昵称不符合规则');
				return;
			}
			ut.request({
				data: {
					name:this.name,
				},
				url: "user/updateName"
			}).then(data=>{
				let userinf=wx.getStorageSync('userinf');
				userinf.name=this.name;
				wx.setStorageSync('userinf',userinf)
				wx.navigateBack()
			})
		},
		checkstr(str){
			let num=0;
			const re = new RegExp("[\\u4e00-\\u9fa5]", "");
			str=str.split('');
			str.forEach(item=>{
				if(re.test(item)){
					num+=2;
				}else{
					num+=1;
				}
			})
			return num;
		}
	}
}



